@import "variables";

.hFlex {
    display: flex;
    flex-direction: row;
}

.vFlex {
    display: flex;
    flex-direction: column;
}

p,div,a,body,li {
    color: black;
    padding: 0;
    margin: 0;
    border: none;
}

ul {
    padding-left: 2ch;
    list-style-type: none;
}

body {
    background-image: url($backgroundImg);
    background-repeat: no-repeat;
    background-size: cover;
}

a {
    text-decoration: underline;
}

iframe {
    display: inline;
}

#siteContainer {
    @extend .vFlex;
    min-height: 100vh;
}

.banner {
    padding: .5rem;
}

.container {
    background-color: rgb(240, 248, 255, .80);
    padding: 1rem;
}

/* SITE header, body, footer */

#siteBanner {
    @extend .hFlex;
    background-color: $accentColor;
    height: 2rem;
    font: {
        variant: small-caps;
        size: 24px;
    }
}

#mainContent {
    @extend .hFlex;
    height: 90vh;
}

#siteFooter {
    font-size: 10px;
    background-color: black;
    color:rgb(171, 171, 171);
    margin-top: auto;
    height: 10px;
}

/* CENTER left, center, right */

#storyNav {
    @extend .vFlex;
    width: 15vw;
    margin: $defaultMargin $defaultMargin $defaultMargin 0;
    border-radius: 0 $defaultBR $defaultBR 0;
    padding-left: 0;
}

#centerCol {
    width: 70vw;
    margin: 0 $defaultMargin 0 $defaultMargin;
    overflow: scroll;
}

#archiveCol {
    width: 15vw;
    margin: $defaultMargin 0 $defaultMargin $defaultMargin;
    border-radius: $defaultBR 0 0 $defaultBR;
}

/* for the navigation bar - baseline behavior */

.navContainer {
    @extend .hFlex;
    flex-wrap: wrap;
    overflow: scroll;
    padding: 0 1rem 1rem 1rem;
}

.storyItem {
    flex: 2 1 26vw;
    height: auto;
    margin: 1ch 0 0 0;
}

#other {
    @extend .hFlex;
    flex-wrap: wrap;
    flex: auto;
}

.snavItem {
    flex: 1 1 26vw;
    margin: 1ch 0 0 0;
}

.sname {
    margin: 0 0 0 0;
}



//headings

h1 {
    font: {
        size: 17px;
        stretch: 50%;
    }

}

.snavLink {
    color: $accentColor2;
    text-decoration: none;
}

#snavContainer {
    border-bottom: 10px ridge $accentColor;
    padding-left: 1rem;
}